.about-body {
  margin: 10px;
  padding: 5em;
  align-items: center;
  height: 80vh;
  text-align: center;
  background-color: rgb(20, 20, 20) !important;
  border-radius: 10px !important;
  border: 1px solid rgb(0, 255, 234) !important;
  box-shadow: 0px 1px 10px rgb(0, 255, 234) inset,
    0px 10px 15px rgba(0, 0, 0, 0.363) !important;
}

.project-link {
  color: rgb(0, 255, 234);
}

.about-body h1 {
  font-size: 2.5rem;
  font-family: "Jockey One", sans-serif;
  color: #fff;
}

.about-body p {
  color: rgb(0, 255, 234);
  font-size: 1.5rem;
  font-family: "Jura", sans-serif;
}

@media screen and (max-width: 1200px) {
  .about-body {
    padding: 3em;
  }
  .about-body h1 {
    font-size: 2rem;
  }
  .about-body p {
    font-size: 1rem;
  }
}
@media screen and (max-width: 770px) {
  .about-body {
    margin: 5px !important;
    margin-top: 0px !important;
    padding: 1em;
    overflow: scroll;
  }
  .about-body h1 {
    font-size: 2rem;
  }
  .about-body p {
    font-size: 0.85rem;
  }
  .about-body h1 {
    font-size: 1.5rem;
  }
  .headshot {
    margin-top: 10px !important;
    height: 100px !important;
    width: auto !important;
  }
}

