
.navbar  {
z-index: 100!important;
}

.navbar h1 {
  margin: 0;
  /* padding-top: 75px; */
  text-align: left;
  color: rgb(0, 255, 234);
  font-size: 3rem;
  font-family: 'Jockey One', sans-serif;
}


.nav-link {
  margin: 0;
  /* padding-top: 75px; */
  float: right;
  color: rgb(0, 255, 234)!important;
  font-size: 1.5rem;
  line-height: 15px;
  font-family: 'Jura', sans-serif;
}

.nav-link:hover {
  margin: 0;
  
  
  float: right;
  color: rgb(255, 255, 255)!important;
  font-size: 1.5rem;
  font-family: 'Jura', sans-serif;
  border-radius: 60px;
  line-height: 15px;

  box-shadow: 0px 1px 6px rgb(0, 255, 234) inset, 0px 5px 10px rgb(0, 255, 234)!important;

}
.active {

  
  color: rgb(255, 255, 255)!important;

  text-shadow: 0px 1px 10px rgb(0, 255, 234)!important;


}
.mobileNav {
  display: none;
}

@media screen and (max-width: 992px) {
  .mobileNav {
    
    display: block;
  }
}
@media screen and (max-width: 770px) {
  .navbar h1 {
    margin: 0;
    /* padding-top: 75px; */
    text-align: left;

    font-size: 2rem;
    font-family: 'Jockey One', sans-serif;
  }
}